import "./App.css";
import React from "react";

export class App extends React.Component {
 

 

  openMultiplicationApp(){
    const url = 'https://npsp2142.github.io/multiplication-app/index.html';
    window.open(url, '_blank');
}

  render() {
    
    return (
      <div className="App">
        <header className="App-header">
          <h1>Jacky Ng</h1>
          <button
            className="App-Menu-Button"
            onClick={(page) => this.openMultiplicationApp(page)}
          >
            <h2>Multiplication App Live Demo</h2>
          </button>
        </header>
      </div>
    );
  }
}

export default App;
